import { useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../../redux/hooks';
import { addFavoriteHeroes, toogleFavoriteHero } from '../../redux/heroesSlice';
import { HiOutlineStar } from 'react-icons/hi';
import css from './Favorites.module.css';
import { FavoriteWithoutHero } from '../../components/FavoriteWithoutHero/FavoriteWithoutHero';

interface IHero {
  id: number;
  localized_name: string;
  img: string;
  attack_type: string;
  primary_attr: string;
  base_str: number;
  str_gain: number;
  base_agi: number;
  agi_gain: number;
  base_int: number;
  int_gain: number;
  roles: [];
  isFavorite: boolean;
}

const Favorites = () => {
  const dispatch = useAppDispatch();
  const favoriteHeroes = useAppSelector(({ heroes }) => heroes.favoriteHeroes);

  useEffect(() => {
    dispatch(addFavoriteHeroes());
  }, [dispatch]);

  const onClick = (event: React.MouseEvent<SVGElement, MouseEvent>) => {
    const id = event.currentTarget.id;
    console.log('hello');
    dispatch(toogleFavoriteHero(id));
    dispatch(addFavoriteHeroes());
  };
  return favoriteHeroes?.length > 0 ? (
    <main className={css.main}>
      <div className={css.container}>
        <ul className={css.card_wrapper}>
          {favoriteHeroes?.map((hero: IHero) => {
            const {
              id,
              localized_name,
              isFavorite,
              img,
              roles,
              base_str,
              base_int,
              base_agi,
              str_gain,
              int_gain,
              agi_gain,
            } = hero;
            return (
              <li key={id} id={id.toString()} className={css.card}>
                <p className={css.card_hero_name}>{localized_name}</p>
                <HiOutlineStar
                  className={css.card_star}
                  id={id.toString()}
                  onClick={onClick}
                  style={{
                    fill: isFavorite ? '#f8e26a' : 'none',
                    stroke: isFavorite ? '#f8e26a' : 'currentColor',
                  }}
                />
                <img
                  src={`https://api.opendota.com${img}`}
                  alt={localized_name}
                  width="264"
                  height="148"
                />

                <p className={css.card_hero_role}>
                  {`Role \u2013`}
                  {roles.map((role, roleIndex) => (
                    <span key={roleIndex}>
                      {role}
                      {roleIndex < hero.roles.length - 1 ? ', ' : ''}
                    </span>
                  ))}
                </p>
                <div className={css.card_characteristics_wrapper}>
                  <span
                    className={`${css.card_characteristics} ${css.card_characteristics_red}`}
                  >{`${base_str} + ${str_gain}`}</span>
                  <span
                    className={`${css.card_characteristics} ${css.card_characteristics_green}`}
                  >{`${base_agi} + ${agi_gain}`}</span>
                  <span
                    className={`${css.card_characteristics} ${css.card_characteristics_blue}`}
                  >{`${base_int} + ${int_gain}`}</span>
                </div>
              </li>
            );
          })}
        </ul>
      </div>
    </main>
  ) : (
    <FavoriteWithoutHero />
  );
};

export default Favorites;
